* {
  box-sizing: border-box;
}

.Game {
  margin: 0 auto;
  width: 450px;
  text-align: center;
  font-family: sans-serif ;
}

.whoNext {
  color: green;
  font-size: 1.2em;
  font-weight: bold;
}

.winner {
  color: blue;
  font-size: 1.5em;
}

.board {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.board-cell {
  height: 150px;
  line-height: 150px;
  font-size: 30px;
  border: 1px solid black;
  flex-basis: 33.3%;
}

.board-cell--x {
  background-image: url(https://image.flaticon.com/icons/png/512/43/43823.png);
  background-size: cover;
}

.board-cell--0 {
  background-image: url(https://svgsilh.com/svg/1093523.svg);
  background-size: cover;
}

.btn {
  margin-top: 50px;
  border: 2px solid black;
  border-radius: 5px;
  background: white;
  font-size: 25px;
  outline: none;
}